import React from "react";
import { Route, Switch } from "react-router-dom";
import {config} from './Constant'
import Home from "./components/home";
import Contacts from "./components/contacts";
import Login from "./components/login";
// import JLPT4 from "./components/jlpt4";
import VocabularyCard from "./components/vocabularycard";
import Logout from "./components/logout"
import NotFound from "./components/notfound";
export default() =>
  <Switch>
    <Route path="/" exact component={Home} />
    <Route path="/login" exact component={Login} />
    <Route path="/logout" exact component={Logout} />
    <Route path="/jlpt4" >
      <VocabularyCard 
          apiUrl={config.url.API_URL+'/jlpt4/random'}
          cardTitle="JLPT N4 Card"
      />
    </Route>
    <Route path="/genkibook">
      <VocabularyCard 
        apiUrl={config.url.API_URL+'/genki/random/1'}
        cardTitle="Genki Book Volume 1 Card"
      />
    </Route>
    <Route path="/contacts">
      <Contacts contact_title="Contact Lists"/>
    </Route>
    <Route component={NotFound}/>
  </Switch>;