import React , {Component} from 'react';
import {BrowserRouter as Router, Link } from "react-router-dom";

import SwitchRoutes from "./Route";
import {AuthProvider} from "./Context";
import './App.css';
// import NavigationBars from './components/navigationbars';

const NavBar = (props)=>{
  return(

        <nav class="navbar navbar-expand-md navbar-dark bg-dark">
          <Link class="navbar-brand" to="/">Digital Bagus</Link>
          <button type="button" class="navbar-toggler" data-toggle="collapse" data-target="#main-menu"
      aria-controls="main-menu" aria-expanded="false" aria-label="Toggle navigation">
      <span class="navbar-toggler-icon"></span>
    </button>
    {props.state.isAuthenticate &&
     
          <div class="collapse navbar-collapse" id="main-menu">
            <ul class="navbar-nav mr-auto">
              
              <li class="nav-item"><Link class="nav-link" to="/">Home<span class="sr-only">(current)</span></Link></li>
              <li class="nav-item"><Link class="nav-link" to="/jlpt4">JLPT N4</Link></li>
              <li class="nav-item"><Link class="nav-link" to="/genkibook">Genki Book</Link></li>
          </ul>
          

          <ul class="navbar-nav navbar-right mr-auto">
              <li class="nav-item"><Link class="nav-link" to="/logout">Logout</Link></li>
              <li class="nav-item"><Link class="nav-link" to="/contacts">Contacts</Link></li>
          </ul>
          
        </div>
}        
  </nav>

)}


export default class App extends Component{
  constructor(props){
    super(props);
    let token = localStorage.getItem('token');
    if(token){
      this.state = {
        isAuthenticate:true,
        token:localStorage.getItem('token')
      };
    }    

  }

  
  state = {
    isAuthenticate: false,
    isLocal : true,
    token : null
  }

  stateAction = (action) => {
    switch(action.type){
      case "LOGIN" :
        if(action.payload.isLocal){
          localStorage.setItem("token",action.payload.token);
        }
        this.setState({
            isAuthenticate:true,
            isLocal:action.payload.isLocal,
            token:action.payload.token
          });
      break;

      case "LOGOUT":
        localStorage.clear();
        this.setState({
          isAuthenticate:false,
          isLocal:true,
          token:null
        });
      break;

      default:
        return null;
    }
  }

  // componentDidMount(){
  //   let token = localStorage.getItem('token');

  // }
  

  render(){
    return(
      <AuthProvider
        value={{
          state: this.state,
          action: this.stateAction
        }}
      >
      <Router>
        <div className="App container">
          <NavBar state={this.state}/>
          <SwitchRoutes/>
        </div>
      </Router>
      </AuthProvider>
    );
  }



}


