import React,{Component} from 'react';
import {Redirect} from 'react-router-dom';
import axios from 'axios';
import './css/Login.css';
import {config} from '../Constant';
import AuthContext, {AuthConsumer} from '../Context';

class LoginBox extends Component{
  constructor(props){
    super(props);
    this.url=config.url.API_URL+'/login'
    this.state={
      usrname : '',
      password:'',
      isSubmitting:false,
      isRemember:true,
      errmsg:null
    }
  }

  static contextType = AuthContext;

  handleInputChange = (event) => {
    //const { value, name } = event.target;
    const value = event.target.type === 'checkbox' ? event.target.checked : event.target.value;
    const name = event.target.name;
    this.setState({
      [name]: value
    });
  }
  

  onSubmit = (event) => {
    event.preventDefault();
    this.setState({isSubmitting:true});
    axios.get(this.url,{
        headers:{
          Accept:'*',
        },
        auth: {
          username: this.state.usrname,
          password: this.state.password
        }
      }).then(data => {
          if(data.status===200){
            this.context.action({
              type:'LOGIN',
              payload:{
                isLocal: this.state.isRemember,
                token:data.data.token
              }
            });            
            // this.state.isRemember ? 
            //   localStorage.setItem('token',data.data.token):
            //   sessionStorage.setItem('token',data.data.token);
          }
        })
        .catch(err=>{
          console.log(err);
          this.setState({isSubmitting:false,errmsg:err.message||err.statusText});

        });
  }

  render(){
    return(
      <div className="login-page">
        <div class="login-box card">
          <div class="card-header">
            <h3>Sign In</h3>
			  	    <div class="d-flex justify-content-end social_icon">
			  		  <span><i class="fab fa-facebook-square"></i></span>
			  		  <span><i class="fab fa-google-plus-square"></i></span>
			  		  <span><i class="fab fa-twitter-square"></i></span>
			  	  </div>
            <div class="card-body">
			  	    <form onSubmit={this.onSubmit}>
			  		    <div class="input-group form-group">
			  			    <div class="input-group-prepend">
			  				  <span class="input-group-text"><i class="fas fa-user"></i></span>
			  			  </div>
                  <input  
                    type="text"
                    class="form-control"
                    name="usrname"
                    placeholder="Enter Username"
                    value={this.state.usrname}
                    onChange={this.handleInputChange}
                    required
                  />
					      </div>

					      <div class="input-group form-group">
					    	  <div class="input-group-prepend">
					    		  <span class="input-group-text"><i class="fas fa-key"></i></span>
					    	  </div>
                  <input
                    type="password"
                    name="password"
                    class="form-control"
                    placeholder="Enter Password"
                    value={this.state.password}
                    onChange={this.handleInputChange}
                    required
                  />
					      </div>
					      <div class="row align-items-center remember">
                  <input  type="checkbox"
                          defaultChecked="true"
                          name = "isRemember"
                          checked={this.isRemember}
                          onChange={this.handleInputChange}/>
                  <label class="form-check-label" for="exampleCheck1">Remember Me</label>
					      </div>
					      <div class="form-group">
						      <input type="submit" value="Login" class="btn float-right login_btn"/>
					      </div>
				    </form>
          </div>
          </div>
        </div>
      </div>
    );
  }
}

class Login extends Component {
  render(){
    return(
      <AuthConsumer>
       {(props) => {
          return props.state.isAuthenticate ?
                  <Redirect to="/"/> : <LoginBox/>
          }
        }
      </AuthConsumer>
    );
  }
}
export default Login